.title {
  text-align: center;
}

table {
  margin-left: auto;
  margin-right: auto;
}

table {
  width: 80%;
}

table, th, tr, td {
  /*border: 2px solid lightblue;
  border-collapse: collapse;*/
  padding: 2px 8px;
}

tr th {
  border-bottom: 2px solid rgb(175, 175, 175);
  border-collapse: collapse;
}

.th-name {
  width: 120px;
}

tr td{
  border-bottom: 1px solid rgb(175, 175, 175);
  border-collapse: collapse;
}

.td-icons {
  width: 80px;
  text-align: center;
}

td img {
  vertical-align: middle;
}

.smaller {
  font-size: smaller;
  font-weight: 400;
}

.icon-js {
  width: 20px;
}

.icon-rt {
  width: 22px;
}

.italic {
  font-style: italic;
}