.inicio {
  margin-top: 120px;
  display: grid;
  grid-template-columns: 3fr 2fr;  
}

.inicio-cv,
.inicio-youtube {
  padding: 10px;
}

.table-cv {
  border: 2px solid rgb(50, 124, 221);
  border-radius: 5px;
  border-collapse: separate;
}

.table-tr-header {
  background-color: rgb(175, 175, 175);
}

.foto-rcl-td {
  width: 0px;
  border-top-left-radius: 10px;
}

.name-title {
  vertical-align: top;
}

.foto-rcl-td, 
.name-title {
  border-bottom: 2px solid rgb(50, 124, 221);
}

.name-title p {
  font-weight: 600;
}

.span-strong {
  font-family: 'consolas';
  font-size: large;
}

.fullstack-ul {
  list-style: "â†’ ";
}

.hr-inicio {
  border: 1px solid rgb(50, 124, 221);
}

.p-center {
  text-align: center;
}

.table-cv {
  margin-left: 20px;
  width: 90%;
}

.table-inicio-contents {
  border: none;
}

.table-cv-tr-td img {
  width: 100px;
}

.inicio-youtube-interno {
  text-align: center;
  border: 2px solid rgb(50, 124, 221);
  border-radius: 5px;
  padding: 10px;
  height: 90%;
}