body {
  font-family: 'Segoe UI';
  font-size: 14px;
  margin: 0;
  background-color: lightgrey;
}

p {
  margin-top: 2px;
  margin-bottom: 3px;
}

.wrapper-header {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 120px;
  /*padding: 5px 5px;*/
}

.wrapper-menu {
  background-color: rgb(175, 175, 175);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex: 2;
}

.menu-portfolio,
.menu-videos {
  background-color: rgb(175, 175, 175);
  /*padding: 10px;*/
  position: relative;
  cursor: pointer;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.portfolio-title,
.videos-title {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 10px;
}

.menu-portfolio:hover,
.menu-videos:hover {
  background-color: rgb(50, 124, 221);
}

.portfolio-title:active,
.videos-title:active {
  background-color: rgb(8, 35, 110);
  color: white;
}

/*-----------Portfolio sub----------------*/
.menu-portfolio-sub {
  background-color: lightsteelblue;
  display: none;
  position: absolute;
  width: 190px;
  left: 0px;
  top: 39px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
}

.subfirst {
  border-top-right-radius: 5px;
}

.sublast {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.menu-portfolio-subi {
  padding: 10px;
}

.menu-portfolio:hover .menu-portfolio-sub{
  display: block;
}

.menu-portfolio-subi:hover {
  background-color: rgb(50, 124, 221);
}

.menu-portfolio-subi:active {
  background-color: rgb(8, 35, 110);
  color: white;
}
/*-----------Portfolio sub----------------*/

/*-----------Videos sub----------------*/
.menu-videos-sub {
  background-color: lightsteelblue;
  display: none;
  position: absolute;
  width: 200px;
  left: 0px;
  top: 39px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
}

.menu-videos-subi {
  padding: 10px;
}

.menu-videos:hover .menu-videos-sub{
  display: block;
}

.menu-videos-sub div:hover {
  background-color: rgb(50, 124, 221);
}

.menu-videos-sub div:active {
  background-color: rgb(8, 35, 110);
  color: white;
}

.menu-videos-3d {
  padding: 5px;
  text-align: center;
  font-variant: small-caps;
}

.menu-videos-3d:hover  {
  background-color: lightsteelblue !important;
  color: black !important;
}

/*-----------Videos sub----------------*/

.wrapper-links {
  background-color: rgb(175, 175, 175);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: end;
  height: 100%;
  flex: 1;
}

.links {
  padding: 0px;
}

.email,
.github,
.linkedin {
  width: 96px;
  height: 54px;  
  background-size: 96px 54px;
  cursor: pointer;
}

.links .email {
  background-image: url('./components/img/email-logo-izq.png');
}

.links .email:hover {
  background-image: url('./components/img/email-logo-der.png');
}

.links .github {  
  background-image: url('./components/img/github-logo-izq.png');
}

.links .github:hover { 
  background-image: url('./components/img/github-logo-der.png');
}

.links .linkedin {
  background-image: url('./components/img/linkedin-logo-izq.png');
}

.links .linkedin:hover {
  background-image: url('./components/img/linkedin-logo-der.png');
}

.link {
  color: black;
  text-decoration: none;
}